(function () {
    var months = ["JANUARY", "FEBRUARY", "MARCH", "APRIL", "MAY", "JUNE", "JULY", "AUGUST", "SEPTEMBER", "OCTOBER", "NOVEMBER", "DECEMBER"];
    var days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

    let today = new Date();
    let currentDay = today.getDate();
    let currentMonth = today.getMonth();
    let currentYear = today.getFullYear();

    let calendar = document.querySelector('.calendar');
    if (calendar) {

        function buildCalendar(month, year, _currentDay) {

            let yearEl = document.querySelector('.year__number .number');
            yearEl.innerText = year;

            let monthActiveEl = document.querySelectorAll('.month__item');
            monthActiveEl.forEach(element => {
                element.classList.remove('active')
            });
            monthActiveEl[month].classList.add('active');

            let days = document.querySelector('.calendar__days');
            let daysInCurrentMonth = daysInMonth(month, year);
            let html = '';
            for (let i = 1; i <= daysInCurrentMonth; i++) {
                html += `<button class="day__item"> <span>${i}</span></button>`
            }
            days.innerHTML = html;

            document.querySelectorAll('.day__item')[_currentDay].classList.add('active');
            document.querySelectorAll('.day__item')[_currentDay].classList.add('dot');

            let dayEls = document.querySelectorAll('.day__item');

            dayEls.forEach((element, index) => {
                element.addEventListener('click', function (e) {
                    dayEls.forEach(element => {
                        element.classList.remove('active');
                    })

                    currentDay = index;
                    dayEls[index].classList.add('active');
                });
            })
        }

        buildCalendar(currentMonth, currentYear, currentDay);
        function daysInMonth(iMonth, iYear) {
            return 32 - new Date(iYear, iMonth, 32).getDate();
        }

        function reset() {
            let today = new Date();
            let _currentDay = today.getDate();
            let _currentMonth = today.getMonth();
            let _currentYear = today.getFullYear();
            buildCalendar(_currentMonth, _currentYear, _currentDay);

        }

        function next() {
            currentYear = ++currentYear;
            buildCalendar(currentMonth, currentYear, currentDay);
        }

        function previous() {
            currentYear = --currentYear;
            buildCalendar(currentMonth, currentYear, currentDay);
        }
        let prevEl = document.querySelector('.year__slider .prev');
        let nextEl = document.querySelector('.year__slider .next');
        prevEl.addEventListener('click', function () {
            previous();
        });
        nextEl.addEventListener('click', function () {
            next();
        })

        let monthEls = document.querySelectorAll('.month__item');
        monthEls.forEach((element, index) => {
            element.addEventListener('click', function (e) {
                currentMonth = index
                buildCalendar(currentMonth, currentYear, currentDay);
            })
        });



        let resetEl = document.querySelector('.reset button');
        resetEl.addEventListener('click', function () {
            reset();
        })
    }
})()